import React,{ Component }  from 'react';
import '../../App.css';
import { Card, Input,Button, Popover,Spin,Modal,Progress } from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';
import web3 from "../../ABI/web3";
import LGC from "../../ABI/LGCget";
import LG from '../../ABI/LG';
import GXzhi from '../../ABI/GxzhiABI';
import erc20abi from '../../ABI/ERC20.abi.json';
import { Link} from 'react-router-dom';
import '../../components/global';

class maincard1 extends Component {
  constructor(props){
    super(props);
    this.state = {
      LGCadr:'0x91de2f1946ba78302a531eeadac95e103f6ac813',  //LGC信用分地址
      key: '账户',
      noTitleKey: '账户',
      user:"",
      oringUser:"",
      LGbalance:"",
      DKJbalance:"",
      YHTbalance:"",
      Airdrop_LGbalance:"",
      GXvalue:"",
      chainId:"",
      GxianID:"",
      chainWarning:"",
      message:"",
      gxSwaped:'',
      Usertotal:'',
      LGCbalance:'',
      LGCclose:'none',
      disabled:'',
      invite:'0x0000000000000000000000000000000000000001', //推荐人
      loadwindow:false, //加载窗
      loading:'block', //加载中
      complete:'none', //加载完成
      loadmessage:'',  //加载窗信息
      chainIdDisabled:'',
  }
}

  async componentDidMount(){
    //判断页面是否安装了Metamask
    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
  
        //初始化Provider
        const provider = window['ethereum']
  
        //获取网络ID
        if(provider.chainId === '0x80'){this.setState({chainId:"火币链"})};
        if(provider.chainId === '0x1'){this.setState({chainId:"以太链"});
          this.setState({chainWarning:"请使用火币链，以太链目前仅可以用“入口”"})};

        if(provider.chainId !== '0x80'){
          this.setState({chainIdDisabled:"disabled"})
          this.setState({message:"您当前帐户非火币链，请切换至火币链"})
        };
        
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()

        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
  
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

    //邀请链接处理
    const invite = {} ;
    const _invite = window.location.search;
    const reg = /[?&][^?&]+=[^?&]+/g;
    const found = _invite.match(reg);
    if(found){
      found.forEach(item => {
        let temp = item.substring(1).split('=');
        let key = temp[0];
        let value = temp[1];
        invite[key] = value;
      })
    }
    if(invite.address !== undefined){
      this.setState({invite:invite.address})
    }
    
    //设置变量中默认用户的帐号
    const _user = window.defaultAccount;
    var userLength = _user.length;
    var user = _user.substring(0,6) + '……' + _user.substring(userLength-4);
    this.setState({
      user,
      oringUser:_user
    });
    
    //获取用户帐号中的LGC余额
    const LGC = new web3.eth.Contract(erc20abi.abi,this.state.LGCadr);
    const LGCbalance = await LGC.methods.balanceOf(window.defaultAccount).call();
    this.setState({LGCbalance:LGCbalance});
    if(LGCbalance === '0'){
      this.setState({LGCclose:'block'})
    }else{
      this.setState({LGCclose:'none'})
    }

    //获取贡献值
    const GXvalue = await GXzhi.methods.getGongxianzhi(window.defaultAccount).call();
    this.setState({GXvalue:parseFloat(web3.utils.fromWei(GXvalue[2],"ether")).toFixed(2)});
    this.setState({gxSwaped:parseFloat(web3.utils.fromWei(GXvalue[3],"ether")).toFixed(2)});
    if(this.state.GXvalue - this.state.gxSwaped === 0){
      this.setState({disabled:'disabled'})
    }

    //获取贡献人数
    const GxianID = await GXzhi.methods.userID().call();
    this.setState({GxianID});
  }

    swapGX =async () => {
      await GXzhi.methods.swapGX().send({from:window.defaultAccount});
    }

    onTabChange = (key, type) => {
      this.setState({ [type]: key });
    };

    _getLGC =async () => {
      await LGC.methods.getLGC(this.state.invite).send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({loadmessage:"打包中……",
          loadwindow: true,
          loading:'block',
          complete:'none'
        })
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"发送成功，请等待审核,通常24小时内通过(获得10信用分即通过)",
          loading:'none',
          complete:'block'
        })
        }
      })
    }

    getLGC =async () => {
      const _LGbalance = await LG.methods.balanceOf(this.state.invite).call();
      const LGbalance = parseFloat(web3.utils.fromWei(_LGbalance,"ether")).toFixed(2);
      if(this.state.invite.toLowerCase() !== '0x0000000000000000000000000000000000000001'){
        if(LGbalance <= 8){
          this.setState({message:'该邀请人LG少于8枚，邀请链接无效'})
        }
      }
      if(this.state.invite.toLowerCase() === window.defaultAccount.toLowerCase()){
        this.setState({message:'不可以自己用自己的邀请链接进入'})
      }
      if(this.state.invite.toLowerCase() !== window.defaultAccount.toLowerCase() && LGbalance >= 8){
        this._getLGC();
      }
      if(this.state.invite.toLowerCase() === '0x0000000000000000000000000000000000000001'){
        this._getLGC();
      }
    }

    //加载窗开关
    loadwindowClose = () => {
      this.setState({loadwindow: false})
    };

    changUser = () =>{
      this.setState({user:this.state.oringUser})
    }

  render(){

    const content1 = (
      <div>
        通过使用生态获得，比如发红包等，将来还会增加更多的生态
      </div>
    );

    const content2 = (
      <div>
        您已运回的LG
      </div>
    );

    const content3 = (
      <div>
        <p>为什么是灰色？挖矿-运回=0；</p>
        <p>快去发个红包吧。</p>
        <p>可以挖多一些之后再一次性运回。</p>
      </div>
    );
    
    const contentListNoTitle = {
      账户: 
        <div>
            <div>
                <div style={{background:"yellow", color:"red",fontSize:"14px"}}>
                  {this.state.chainWarning}
                </div>
            </div>
            <div onClick={() => {this.changUser()}} style={{marginBottom:'5px'}}>
              <Input id='i1' addonBefore={this.state.chainId} value={this.state.user} disabled="true"/>
            </div>
            <Link to='/LGCtransfer'>
                <div to='/LGCtransfer' className="gongxian"><QuestionCircleOutlined />信用分(LGC)<span style={{float:"right"}}>{this.state.LGCbalance}</span></div>
            </Link>
                <div style={{display:this.state.LGCclose, marginTop:'10px'}} >
                    <div>链改网欢迎您，为避免撸客捣乱，注有:LGC&ge;10的生态，需申请信用分</div>
                    <Button  onClick={() => {this.getLGC()}} 
                          disabled={this.state.chainIdDisabled}
                          type="primary"  
                          size='larger'
                          shape='round'
                          block >
                          申请信用分
                      </Button>
                      <div style={{color:"green",fontSize:"12px"}}>
                        {this.state.message}
                      </div>
                </div>

                {/* 加载窗  */}
                <Modal  visible={this.state.loadwindow} 
                    onCancel={this.loadwindowClose} 
                    footer={[
                      <div style={{textAlign:'center'}} >
                        <Button type='primary' onClick={this.loadwindowClose}>关闭</Button>
                      </div>,
                      ]}
                  >
                    <div style={{textAlign:'center'}}>
                          <div style={{display:this.state.loading}}>
                            <Spin size="large" />
                            <div style={{color:"#1890ff",fontSize:"13px",textAlign:'center'}}>
                              {this.state.loadmessage}
                            </div>
                          </div>
                          <div style={{display:this.state.complete}}>
                            <Progress type="circle" percent={100} width={45}/>
                            <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                              {this.state.loadmessage}
                            </div>
                          </div>
                    </div>
                </Modal>         
        </div>,
      挖矿: 
          <div>
              <p className="gongxian">
                  <Popover content={content1} trigger="click">
                        <QuestionCircleOutlined /> 
                  </Popover>
                  &nbsp;&nbsp;生态挖矿
                  <span style={{float:"right"}}>{this.state.GXvalue}</span>
              </p>
              <p className="gongxian">
                    <Popover content={content2} trigger="click">
                        <QuestionCircleOutlined /> 
                    </Popover>
                    &nbsp;&nbsp;挖矿运回<span style={{float:"right"}}>{this.state.gxSwaped}</span></p>

              <span style={{float:'right'}}>
                  <Popover content={content3} trigger="click">
                        <QuestionCircleOutlined /> 
                  </Popover>&nbsp;&nbsp;
                  <Button onClick={() => {this.swapGX()}} 
                        disabled={this.state.disabled}
                        type="primary"  
                        size='small'>
                        运回
                    </Button>
              </span>
              
          </div>
          ,
    };
  return (
    <Card type="inner" size='small' style={{borderRadius:'10px'}}
      bodyStyle={{paddingTop:'8px',paddingBottom:'8px'}}
      activeTabKey={this.state.noTitleKey}
      onTabChange={key => {
        this.onTabChange(key, 'noTitleKey');
      }}
    >
      {contentListNoTitle[this.state.noTitleKey]}
    </Card>
  );
}
}



export default maincard1;
