import React, { Component } from 'react';
import ReactECharts from 'echarts-for-react';
import { Card } from 'antd';

class price extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }
    }

    componentDidMount(){

        
            
}
    
    render() {
        const options = {
            
            tooltip : {
                trigger: 'axis',
                axisPointer : {      // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'    // 默认为直线，可选为：'line' | 'shadow'
                }
            },
              
            grid: {
                top:'3%',
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: {
                type : 'category',
                splitLine: {show:false},
                data :  ["11月1日", "11月2日", "11月3日", "11月4日", "11月5日", "11月6日", "11月7日", "11月8日", "11月9日", "11月10日", "11月11日"]
            },
            yAxis: {
                type : 'value'
            },
            series: [
                {
                    name: '价格',
                    type: 'bar',
                    stack: 'price',
                    itemStyle: {
                    normal: {
                      barBorderColor: 'rgba(0,0,0,0)',
                      color: 'rgba(0,0,0,0)'
                    },
                    emphasis: {
                      barBorderColor: 'rgba(0,0,0,0)',
                      color: 'rgba(0,0,0,0)'
                    }
                },
                  data: [0, 900, 1245, 1530, 1376, 1376, 1511, 1689, 1856, 1495, 1292]
                },
                {
                    name: '涨',
                    type: 'bar',
                    stack: 'price',
                    color: 'rgb(3,173,144)',
                    data: [900, 345, 393, 0, 0, 135, 178, 286, 0, 0,0]
                },
                {
                    name: '跌',
                    type: 'bar',
                    stack: 'price',
                    color:'rgb(209,75,100)',
                    data: [0, 0, 0, 108, 154, 0,0,0, 119, 361, 203]
                }
               
            ]
        };
        return (
            <div>
                <Card size='small' >
                    <ReactECharts option={options} />;
                </Card>
            </div>
        )
    }
}
export default price;