import React,{ Component }  from 'react';
import '../../App.css';
import { Card ,Button,Input,Popover } from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';
import HangQin from '../../ABI/hangqin';

class Hangqinadd extends Component {
      state = {
        tokenA:'',
        tokenB:'',
        tokenUSD:'',
        tokenABpool:'',
        tokenBUSDpool:'',
        disabledB:'',
        disabledU:'',
        disabledBU:'',
        isU:''
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
        //初始化Provider
        const provider = window['ethereum']
        if(provider.chainId === '0x1'){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

  }

  isHT = async()=>{
    this.setState({
      isU:'',
      tokenB:'0x5545153ccfca01fbd7dd11c0b23ba694d9509a6f',
      tokenUSD:'0xa71EdC38d189767582C38A3145b5873052c3e47a',
      tokenBUSDpool:'0x499B6E03749B4bAF95F9E70EeD5355b138EA6C31',
      disabledB:'disabled',
      disabledU:'disabled',
      disabledBU:'disabled',
    })
  }

  isU = async()=>{
    this.setState({
      isU:'1',
      tokenUSD:this.state.tokenB,
      tokenBUSDpool:this.state.tokenABpool,
      tokenABpool:'',
      tokenB:'',
      disabledB:'',
      disabledU:'disabled',
      disabledBU:'disabled',
    })
  }

  addToken = async () =>{
    if(this.state.isU === '1'){
      this.setState({
        tokenBUSDpool:this.state.tokenABpool,
        tokenUSD:this.state.tokenB
      })
    }
    await HangQin.methods.addToken(
        this.state.tokenA,
        this.state.tokenB,
        this.state.tokenUSD,
        this.state.tokenABpool,
        this.state.tokenBUSDpool
    ).send({from:window.defaultAccount});
  }

  userModify = async () =>{
    if(this.state.isU === '1'){
      this.setState({
        tokenBUSDpool:this.state.tokenABpool,
        tokenUSD:this.state.tokenB
      })
    }
    await HangQin.methods.userModify(
      this.state.tokenA,
      this.state.tokenB,
      this.state.tokenUSD,
      this.state.tokenABpool,
      this.state.tokenBUSDpool
    ).send({from:window.defaultAccount});
  }


  render(){

      const content1 = (
        <div>
          您希望显示的通证合约地址
        </div>
      );
      const content2 = (
        <div>
          <p>与通证A交易的币种合约地址</p>
        </div>
      );
      const content3 = (
        <div>
          <p>与通证B交易的稳定币种合约地址</p>
        </div>
      );
      const content4 = (
        <div>
          <p>通证A与通证B交易的交易对合约地址</p>
        </div>
      );
      const content5 = (
        <div>
          <p>通证B与稳定币U交易的交易对合约地址</p>
        </div>
      );

  return (
    <div>
        <Card type="inner" size='small' title="任何人可以将任何通证添加到行情页" 
          style={{borderRadius:'10px'}}
          headStyle={{borderTopLeftRadius:'10px',borderTopRightRadius:'10px'}}
        >
                <div style={{marginTop:'5px'}}>通证A：
                      <Popover content={content1} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>
                      </div>
                      <Input   value={this.state.tokenA} placeholder="输入合约地址" 
                      onChange={event=>{this.setState({tokenA:event.target.value})}}/>
                <div style={{marginTop:'5px'}}>通证B：
                      <Popover content={content2} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>&emsp;
                      <Button  type="primary"  size="small" style={{marginTop:'6px'}} 
                        onClick={() => {this.isHT()}}>通证B=HT</Button>&emsp;
                      <Button  type="primary"  size="small" style={{marginTop:'6px'}} 
                        onClick={() => {this.isU()}}>通证B=稳定币</Button>
                      </div>
                      <Input   value={this.state.tokenB} placeholder="输入合约地址" 
                        disabled={this.state.disabledB}
                        onChange={event=>{this.setState({tokenB:event.target.value})}}/>
                <div style={{marginTop:'5px'}}>稳定币U：
                      <Popover content={content3} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>
                      </div>
                      <Input   value={this.state.tokenUSD} placeholder="输入合约地址" 
                        disabled={this.state.disabledU}
                        onChange={event=>{this.setState({tokenUSD:event.target.value})}}/>

                <div style={{marginTop:'5px'}}>A/B交易地址：
                      <Popover content={content4} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>
                </div>
                <Input  value={this.state.tokenABpool} placeholder="A/B交易对地址" 
                      onChange={event=>{this.setState({tokenABpool:event.target.value})}}/>
                
                <div style={{marginTop:'5px'}}>B/U交易地址
                      <Popover content={content5} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>
                </div>
                <Input  value={this.state.tokenBUSDpool} placeholder="B/U交易对地址" 
                  disabled={this.state.disabledBU}
                  onChange={event=>{this.setState({tokenBUSDpool:event.target.value})}}/>

                <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'6px'}} 
                      onClick={() => {this.addToken()}}>添加</Button>
         
              <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'10px'}} 
                      onClick={() => {this.userModify()}}>修改</Button>
         </Card>
    </div>
  );
}
}

export default Hangqinadd;
